import { axios } from '@/utils/request'

/**
 * @descrption 
 *
 * @api login: 
 * @api logout: 
 * @api modifyPassword: 
 */
const api = {
  login: '/auth/login',
  logout: '/auth/logout',
  modifyPassword: '/auth/modifyPassword'
}

export function login (data) {
  return axios({
    url: api.login,
    method: 'post',
    data
  })
}

export function logout (data) {
  return axios({
    url: api.logout,
    method: 'post',
    data
  })
}

export function modifyPassword (data) {
  return axios({
    url: api.modifyPassword,
    method: 'post',
    data
  })
}

export default api
