import Mock from 'mockjs2'
import Utils from '@/mock/utils'
import api from '@/api/organize'

/**
 * @descrption  - Tree
 */
Mock.mock(Utils.toRegex(api.getOrganizeInfoTree), 'post', options => {
  // 
  const log = console.log
  const body = Utils.getBody(options)

  // 
  let result = null
  switch (body && body.param && body.param.orgId) {
    case '0': {
      result = Utils.builder([
        {
          key: '1',
          isOrg: 'Y',
          isLeaf: false,
          title: '',
          orgShortName: ''
        }
      ])
      break
    }
    case '1': {
      result = Utils.builder([
        {
          key: '101.100',
          isOrg: 'Y',
          isLeaf: false,
          title: '',
          orgShortName: ''
        }
      ])
      break
    }
    case '101.100': {
      result = Utils.builder([
        {
          key: '101.100.131',
          isOrg: 'Y',
          isLeaf: true,
          title: '',
          orgShortName: ''
        },
        {
          key: '101.100.138',
          isOrg: 'Y',
          isLeaf: true,
          title: '',
          orgShortName: ''
        }
      ])
      break
    }
    default: {
      result = Utils.builder([])
    }
  }

  // 
  log('\n\n---  - Tree ---------')
  log(body)

  log('\n\n---  - Tree ---------')
  log(result)

  // 
  return result
})
