import { extender } from '@antd-templater/library-2.x/SForm/Helper'

/**
 * @description  - 
 */
export default {
  data () {
    // 
    const FORM_BASE = [
      {
        type: 'AGroup',
        slot: 'title1',
        field: 'title1',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'AInput',
        slot: 'roleName',
        field: 'roleName',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'ASelect',
        slot: 'dataFlag',
        field: 'dataFlag',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      }
    ]

    // 
    const FORM_OTHER = [
      {
        type: 'AGroup',
        slot: 'title2',
        field: 'title2',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'ASwitch',
        slot: 'activity',
        field: 'activity',
        label: '',
        decorator: {
          valuePropName: 'checked'
        },
        attrs: {
          checkedChildren: '',
          unCheckedChildren: ''
        }
      }
    ]

    // 
    const groups = [
      ...FORM_BASE,
      ...FORM_OTHER
    ]

    return {
      // 
      grid: {
        gutter: 8,
        xs: 24
      },

      // 
      attrs: {
        labelAlign: 'right'
      },

      // 
      watch: {
        onValuesChange (props, values) {
          // 
          const changed = {}

          //  ()
          if (Object.keys(changed).length > 0) {
            this.setFieldsValue(changed)
          }
        }
      },

      // 
      groups: [
        ...extender.groups(groups, (group, index, groups) => {

        })
      ],

      // 
      options: {
        ...extender.options(groups, (group, index, groups) => {
          // 
          const option = {}

          // 
          if (
            [
              'ASelect',
              'ATreeSelect',
              'ACascader',
              'ARadioGroup',
              'ACheckboxGroup',
              'AAutoComplete'
            ].includes(group.type)
          ) {
            Object.assign(option, {
              selectOptions: []
            })
          }

          // 
          if (group.field === 'dataFlag') {
            Object.assign(option, {
              selectOptions: this.queryOptions.dataFlag
            })
          }

          return option
        })
      },

      //  (props)
      // disabled: false,

      //  (props)
      // readonly: false,

      // 
      spinning: false
    }
  }
}
