import { validator, extender } from '@antd-templater/library-2.x/SForm/Helper'

/**
 * @description  - 
 */
export default {
  data () {
    // 
    const FORM_BASE = [
      {
        type: 'AGroup',
        slot: '',
        field: 'title1',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'ASearch',
        slot: 'userNo',
        field: 'userNo',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'AInputPassword',
        slot: 'password',
        field: 'password',
        label: '',
        decorator: {
          rules: [
            validator.password({
              message: '',
              validator: '',
              pattern: /.{6,}/,
              required: true
            })
          ]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'AInput',
        slot: 'userName',
        field: 'userName',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'AInput',
        slot: 'mobilePhone',
        field: 'mobilePhone',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'AInput',
        slot: 'idCard',
        field: 'idCard',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'ATreeSelect',
        slot: 'orgId',
        field: 'orgId',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          dropdownClassName: 'text-ellipsis',
          dropdownStyle: {
            maxHeight: '300px'
          },
          allowClear: true,
          showSearch: true,
          dropdownMatchSelectWidth: true,
          treeNodeFilterProp: 'label',
          placeholder: ''
        }
      },
      {
        type: 'ASelect',
        slot: 'deptId',
        field: 'deptId',
        label: '',
        decorator: {},
        attrs: {
          allowClear: true,
          placeholder: ''
        }
      },
      {
        type: 'ASelect',
        slot: 'roleId',
        field: 'roleId',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          mode: 'multiple',
          placeholder: '',
          replaceFields: { key: 'roleId', label: 'roleName', value: 'roleId' },
          allowClear: true
        }
      }
    ]

    // 
    const FORM_OTHER = [
      {
        type: 'AGroup',
        slot: 'title2',
        field: 'title2',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'ASwitch',
        slot: 'activity',
        field: 'activity',
        label: '',
        decorator: {
          valuePropName: 'checked'
        },
        attrs: {
          checkedChildren: '',
          unCheckedChildren: ''
        }
      }
    ]

    // 
    const groups = [
      ...FORM_BASE,
      ...FORM_OTHER
    ]

    return {
      // 
      grid: {
        gutter: 8,
        xs: 24
      },

      // 
      attrs: {
        labelAlign: 'right'
      },

      // 
      watch: {
        onValuesChange (props, values) {
          // 
          const changed = {}

          //  ()
          if (Object.keys(changed).length > 0) {
            this.setFieldsValue(changed)
          }
        }
      },

      // 
      groups: [
        ...extender.groups(groups, (group, index, groups) => {

        })
      ],

      // 
      options: {
        ...extender.options(groups, (group, index, groups) => {
          // 
          const that = this
          const option = {}

          // 
          if (
            [
              'ASelect',
              'ATreeSelect',
              'ACascader',
              'ARadioGroup',
              'ACheckboxGroup',
              'AAutoComplete'
            ].includes(group.type)
          ) {
            Object.assign(option, {
              selectOptions: []
            })
          }

          // 
          if (group.field === 'orgId') {
            Object.assign(option, {
              handleEvents: {
                change (value) {
                  const deptId = that.getFieldValue('deptId')
                  const deptIdArr =
                    that.options.deptId.selectOptions
                  if (
                    !deptIdArr.some(
                      item =>
                        deptId === item.value &&
                        value === item.parentId
                    )
                  ) {
                    that.setFieldsValue({ deptId: '' })
                  }
                }
              },
              selectOptions: this.queryOptions.orgTree
            })
          }

          // 
          if (group.field === 'deptId') {
            Object.assign(option, {
              selectOptionsRender (options, { Util, form }) {
                return options.filter(opt => {
                  const orgId = form.getFieldValue('orgId')
                  return orgId && opt.parentId === orgId
                })
              },
              selectOptions: this.queryOptions.deptId
            })
          }

          // 
          if (group.field === 'roleId') {
            Object.assign(option, {
              selectOptions: this.queryOptions.roleId
            })
          }

          return option
        })
      },

      //  (props)
      // disabled: false,

      //  (props)
      // readonly: false,

      // 
      spinning: false
    }
  }
}
