<template>
  <section class="organize-drawer-form-container">
    <s-form
      ref="form"
      :grid="grid"
      :watch="watch"
      :attrs="attrs"
      :groups="groups"
      :options="options"
      :disabled="disabled"
      :readonly="readonly"
      :spinning="spinning"
    />
  </section>
</template>

<script>
import { requestBuilder } from '@/utils/util'
import * as organizeApi from '@/api/organize'

// 
import FormConfig from './OrganizeDrawerForm.js'

export default {
  name: 'OrganizeDrawerForm',
  mixins: [FormConfig],
  props: {
    queryOptions: {
      type: Object,
      default: function () {
        return {
          orgTree: [],
          isOrg: []
        }
      }
    },
    rootNodes: {
      type: Array,
      default: function () {
        return []
      }
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      action: '',
      record: {}
    }
  },
  watch: {
    loading: {
      handler (loading) {
        this.spinning = !!loading
      },
      immediate: true
    },
    spinning: {
      handler (spinning) {
        this.$emit('update:loading', !!spinning)
      }
    }
  },
  methods: {
    /**
     * @description 
     * @param {Array}
     * @returns {undefined}
     */
    resetFields () {
      return this.$refs.form.resetFields(...arguments)
    },

    /**
     * @description 
     * @param {Object}
     * @returns {undefined}
     */
    setFieldsValue () {
      this.$refs.form.setFieldsValue(...arguments)
    },

    /**
     * @description  - 
     * @param {Array}
     * @returns {Object}
     */
    getFieldsValue () {
      return this.$refs.form.getFieldsValue(...arguments)
    },

    /**
     * @description  - 
     * @param {String}
     * @returns {String}
     */
    getFieldValue () {
      return this.$refs.form.getFieldValue(...arguments)
    },

    /**
     * @description 
     * @param {Function}
     * @returns {undefined}
     */
    validateFields () {
      return this.$refs.form.validateFields(...arguments)
    },

    /**
     * @description 
     * @param {String} action
     * @param {Object} record
     * @returns {undefined}
     */
    doCreateForm (action = '', record = {}) {
      this.action = action
      this.record = record
      this.resetFields()
      this.$nextTick(() => this.setFieldsValue(record))
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doDestroyForm () {
      this.action = ''
      this.record = {}
      this.resetFields()
    },

    /**
     * @description 
     * @param {Array} records
     * @returns {Promise}
     */
    doDeleteData (records) {
      return organizeApi.deleteOrganizeInfo(requestBuilder('delete', records)).then(res => {
        if (res.code !== '0000') {
          this.$notification.error({
            message: '',
            description: res.message || '!'
          })
          return Promise.reject(res)
        }
        this.$notification.success({
          message: '',
          description: '!'
        })
      })
    },

    /**
     * @description 
     * @param {Object} record
     * @returns {Promise}
     */
    doValidateData (record) {
      return new Promise((resolve, reject) => {
        this.validateFields(errors => {
          if (errors) {
            this.$message.error('!')
            reject(new Error('!'))
            return
          }

          const data = {
            ...record,
            ...this.getFieldsValue()
          }

          resolve([
            {
              ...data,
              parentOrgId: data.parentOrgId || '0'
            }
          ])
        })
      })
    },

    /**
     * @description 
     * @param {Object} record
     * @returns {Promise}
     */
    doUpdateData (record) {
      return this.doValidateData(record).then(records => {
        if (!this.spinning) {
          // 
          this.spinning = true

          // 
          const notice = {
            error: this.action === 'insert' ? '!' : '!',
            success: this.action === 'insert' ? '!' : '!'
          }

          // 
          const promise =
            this.action === 'insert'
              ? organizeApi.addOrganizeInfo(requestBuilder(this.action, records[0]))
              : organizeApi.modifyOrganizeInfo(requestBuilder(this.action, records[0]))

          // 
          return promise
            .then(res => {
              if (res.code !== '0000') {
                this.$notification.error({
                  message: '',
                  description: res.message || notice.error
                })
                return Promise.reject(res)
              }
              this.$notification.success({
                message: '',
                description: notice.success
              })
              return Promise.resolve(records[0])
            })
            .finally(() => {
              this.spinning = false
            })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
:deep(.s-form-container) {
  .ant-form.ant-form-horizontal {
    .ant-form-item > .ant-form-item-label {
      width: 54px;
      margin-right: 5px;
    }
  }
}
</style>
