import { extender } from '@antd-templater/library-2.x/SForm/Helper'

/**
 * @description  - 
 */
export default {
  data () {
    // 
    const FORM_BASE = [
      {
        type: 'AGroup',
        slot: '',
        field: 'title1',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'AAutoComplete',
        slot: 'title',
        field: 'title',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: '',
          replaceFields: { label: 'label', value: 'label' }
        }
      },
      {
        type: 'AInput',
        slot: 'resourceName',
        field: 'resourceName',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'AInput',
        slot: 'sort',
        field: 'sort',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      }
    ]

    // 
    const FORM_OTHER = [
      {
        type: 'AGroup',
        slot: 'title2',
        field: 'title2',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'ASwitch',
        slot: 'activity',
        field: 'activity',
        label: '',
        decorator: {
          valuePropName: 'checked'
        },
        attrs: {
          checkedChildren: '',
          unCheckedChildren: ''
        }
      }
    ]

    // 
    const groups = [
      ...FORM_BASE,
      ...FORM_OTHER
    ]

    return {
      // 
      grid: {
        gutter: 8,
        xs: 24
      },

      // 
      attrs: {
        labelAlign: 'right'
      },

      // 
      watch: {
        onValuesChange (props, values) {
          // 
          const changed = {}

          //  ()
          if (Object.keys(changed).length > 0) {
            this.setFieldsValue(changed)
          }
        }
      },

      // 
      groups: [
        ...extender.groups(groups, (group, index, groups) => {

        })
      ],

      // 
      options: {
        ...extender.options(groups, (group, index, groups) => {
          // 
          const that = this
          const option = {}

          // 
          if (
            [
              'ASelect',
              'ATreeSelect',
              'ACascader',
              'ARadioGroup',
              'ACheckboxGroup',
              'AAutoComplete'
            ].includes(group.type)
          ) {
            Object.assign(option, {
              selectOptions: []
            })
          }

          // 
          if (group.field === 'title') {
            Object.assign(option, {
              selectOptions: this.queryOptions.buttons,
              selectOptionsRender (options, { form }) {
                if (!form.getFieldValue('title')) {
                  return options
                }
                return options.filter(option => {
                  return (
                    options.some(
                      option =>
                        option.label.toLocaleLowerCase() ===
                        form.getFieldValue('title').toLocaleLowerCase()
                    ) ||
                    option.label
                      .toLocaleLowerCase()
                      .indexOf(
                        form.getFieldValue('title').toLocaleLowerCase()
                      ) !== -1
                  )
                })
              },
              handleEvents: {
                select (value) {
                  const resourceOptions = that.options.title.selectOptions
                  const resource = resourceOptions.find(
                    option => option.label === value
                  )
                  that.setFieldsValue({
                    resourceName: (resource && resource.value) || '',
                    sort: (resource && resource.sort) || ''
                  })
                }
              }
            })
          }

          return option
        })
      },

      //  (props)
      // disabled: false,

      //  (props)
      // readonly: false,

      // 
      spinning: false
    }
  }
}
