import { extender } from '@antd-templater/library-2.x/SForm/Helper'

/**
 * @description  - 
 */
export default {
  data () {
    // 
    const FORM_BASE = [
      {
        type: 'AGroup',
        slot: '',
        field: 'title1',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'ATextarea',
        slot: 'title',
        field: 'title',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          rows: 1,
          autoSize: true,
          placeholder: ''
        }
      },
      {
        type: 'AInput',
        slot: 'orgShortName',
        field: 'orgShortName',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'ASelect',
        slot: 'isOrg',
        field: 'isOrg',
        label: '',
        decorator: {
          rules: [{ required: true, message: '' }]
        },
        attrs: {
          placeholder: ''
        }
      },
      {
        type: 'ATreeSelect',
        slot: 'parentOrgId',
        field: 'parentOrgId',
        label: '',
        decorator: {},
        attrs: {
          dropdownClassName: 'text-ellipsis',
          dropdownStyle: {
            maxHeight: '300px'
          },
          allowClear: true,
          showSearch: true,
          dropdownMatchSelectWidth: true,
          treeNodeFilterProp: 'label',
          placeholder: ''
        }
      }
    ]

    // 
    const FORM_OTHER = [
      {
        type: 'AGroup',
        slot: 'title2',
        field: 'title2',
        title: '',
        attrs: {},
        grid: {}
      },
      {
        type: 'ASwitch',
        slot: 'activity',
        field: 'activity',
        label: '',
        decorator: {
          valuePropName: 'checked'
        },
        attrs: {
          checkedChildren: '',
          unCheckedChildren: ''
        }
      }
    ]

    // 
    const groups = [
      ...FORM_BASE,
      ...FORM_OTHER
    ]

    return {
      // 
      grid: {
        gutter: 8,
        xs: 24
      },

      // 
      attrs: {
        labelAlign: 'right'
      },

      // 
      watch: {
        onValuesChange (props, values) {
          // 
          const changed = {}

          // 
          if (values.isOrg === 'N') {
            Object.assign(changed, {
              orgShortName: ''
            })
          }

          // 
          if (values.parentOrgId === '0') {
            Object.assign(changed, {
              parentOrgId: ''
            })
          }

          //  ()
          if (Object.keys(changed).length > 0) {
            this.setFieldsValue(changed)
          }
        }
      },

      // 
      groups: [
        ...extender.groups(groups, (group, index, groups) => {

        })
      ],

      // 
      options: {
        ...extender.options(groups, (group, index, groups) => {
          // 
          const that = this
          const option = {}

          // 
          if (
            [
              'ASelect',
              'ATreeSelect',
              'ACascader',
              'ARadioGroup',
              'ACheckboxGroup',
              'AAutoComplete'
            ].includes(group.type)
          ) {
            Object.assign(option, {
              selectOptions: []
            })
          }

          // 
          if (group.field === 'orgShortName') {
            Object.assign(option, {
              handleRender (item, { Util, form }) {
                return form.getFieldValue('isOrg') !== 'N'
              }
            })
          }

          // 
          if (group.field === 'isOrg') {
            Object.assign(option, {
              handleAttrs () {
                return {
                  disabled: that.action !== 'insert'
                }
              },
              selectOptions: this.queryOptions.isOrg
            })
          }

          // 
          if (group.field === 'parentOrgId') {
            Object.assign(option, {
              selectOptionsRender (options, { Util, form }) {
                return options.filter(opt => {
                  if (that.action !== 'insert') {
                    const key = that.record.key
                    return !key || opt.value !== key
                  }
                  return true
                })
              },
              selectOptions: this.queryOptions.orgTree
            })
          }

          // 
          if (group.field === 'activity') {
            Object.assign(option, {
              handleAttrs (item, { form }) {
                return {
                  disabled: that.rootNodes.some(
                    node => node.value === that.record.key
                  )
                }
              }
            })
          }

          return option
        })
      },

      //  (props)
      // disabled: false,

      //  (props)
      // readonly: false,

      // 
      spinning: false
    }
  }
}
