<template>
  <section class="user-table-container">
    <s-table
      ref="table"
      :data="loadData"
      :scroll="scroll"
      :columns="columns"
      :customRow="rowClick"
      :clearSelection="clearSelection"
      :showPagination="showPagination"
      :rowClassName="rowClassName"
      :pagination="pagination"
      :immediate="immediate"
      :bordered="bordered"
      rowKey="id"
      striped
    >
      <template #orgId="key">
        <s-ellipsis
          :tooltip="true"
          :sheared="false"
          :length="16"
        >
          {{ takeLabelByKey(queryOptions.orgTree, key, ['shortName', 'label']) }}
        </s-ellipsis>
      </template>

      <template #deptId="key">
        <s-ellipsis
          :tooltip="true"
          :sheared="false"
          :length="16"
        >
          {{ takeLabelByKey(queryOptions.deptId, key) }}
        </s-ellipsis>
      </template>

      <template #activity="value, record">
        <a-switch
          :checked="value === 'Y'"
          checkedChildren=""
          unCheckedChildren=""
          @click.native.stop="() => {}"
          @change="doActivityChange(record, 'activity', $event)"
        />
      </template>

      <template #modifyDate="date">
        <span>{{ takeTimeToFormat(date, 'YYYY-MM-DD HH:mm:ss') }}</span>
      </template>

      <template #action="text, record">
        <a
          href="javascript: void(0)"
          style="display: inline-block; margin: 7px"
          @click.stop="doDrawerEdit(record)"
        >
          
        </a>
      </template>
    </s-table>

    <!--  -->
    <user-drawer
      ref="userDrawer"
      :queryOptions="queryOptions"
      @submitted="doTableQuery"
      @deleted="doTableQuery"
    />
  </section>
</template>

<script>
import { requestBuilder, deepUpdate, takeLabelByKey, takeTimeToFormat } from '@/utils/util'
import * as roleApi from '@/api/role'
import * as userApi from '@/api/user'

import UserDrawer from './UserDrawer'

export default {
  name: 'UserTable',
  components: {
    UserDrawer
  },
  props: {
    queryOptions: {
      type: Object,
      default: function () {
        return {
          orgTree: [],
          deptId: [],
          roleId: []
        }
      }
    },
    pagination: {
      type: Object,
      default: function () {
        return {
          total: 0,
          current: 1,
          pageSize: 20,
          pageSizeOptions: ['10', '20', '25', '30', '50', '100', '200'],
          showTotal: (total, range) => ` ${range[0]}-${range[1]}  ( ${total} )`,
          hideOnSinglePage: false,
          showSizeChanger: true,
          showLessItems: true
        }
      }
    }
  },
  data () {
    return {
      // 
      queryParam: {
        userNo: '',
        userName: '',
        mobilePhone: '',
        activity: ''
      },

      // 
      columns: [
        {
          title: '',
          dataIndex: 'userNo',
          ellipsis: true,
          width: 130
        },
        {
          title: '',
          dataIndex: 'userName',
          ellipsis: true,
          width: 130
        },
        {
          title: '',
          dataIndex: 'mobilePhone',
          ellipsis: true,
          width: 120
        },
        {
          title: '',
          dataIndex: 'postName',
          width: 120
        },
        {
          title: '',
          dataIndex: 'orgId',
          scopedSlots: { customRender: 'orgId' },
          ellipsis: true,
          width: 120
        },
        {
          title: '',
          dataIndex: 'deptId',
          scopedSlots: { customRender: 'deptId' },
          ellipsis: true,
          width: 120
        },
        {
          title: '',
          dataIndex: 'activity',
          scopedSlots: { customRender: 'activity' },
          align: 'center',
          width: 80
        },
        {
          title: '',
          dataIndex: 'action',
          scopedSlots: { customRender: 'action' },
          align: 'center',
          width: 80
        }
      ],
      scroll: {
        x: '100%',
        scrollToFirstRowOnChange: false
      },
      rowSelection: {
        type: 'checkbox',
        onChange: this.doTableChange
      },
      dataSource: [],
      selectedRows: [],
      selectedRowKeys: [],
      clearSelection: true,
      showPagination: false,
      immediate: true,
      bordered: false,
      loadData: parameter => {
        const param = requestBuilder(
          '',
          deepUpdate(
            {
              userNo: '',
              userName: '',
              mobilePhone: '',
              activity: ''
            },
            this.queryParam
          ),
          parameter.pageNo,
          parameter.pageSize,
          parameter.sortField,
          parameter.sortOrder
        )
        return userApi.getUserInfoList(param).then(res => {
          if (res.code !== '0000') {
            this.$notification.error({
              message: '',
              description: res.message || '!'
            })
            return Promise.reject(res)
          }

          // 
          this.dataSource = res.result.data

          // 
          return res
        })
      },
      rowClick: record => ({
        on: {}
      }),
      rowClassName: record => {
        return ''
      }
    }
  },
  mounted () {
    this.doResizeObserve()
  },
  destroyed () {
    this.doResizeDestroy()
  },
  methods: {
    /**
     * @description 
     * @param {Array} tree
     * @param {String | Number} key
     * @param {String} value?
     * @param {String} children?
     * @return {String}
     */
    takeLabelByKey,

    /**
     * @description  
     * @param {Date | String | Number} date
     * @param {String} format?
     * @return {String}
     */
    takeTimeToFormat,

    /**
     * @description 
     * @returns {undefined}
     */
    doResizeObserve () {
      if (window.ResizeObserver) {
        const optimize = () => {
          return () => {
            const $table = this.$refs.table.$el
            const $parent = $table.parentElement
            const $thead = $table.querySelector('.ant-table-fixed > .ant-table-thead')
            const parentHeight = ($parent && $parent.clientHeight) || 0
            const theadHeight = ($thead && $thead.clientHeight) || 0
            const scrollHeight = parentHeight - theadHeight

            if (scrollHeight) {
              this.scroll = {
                ...this.scroll,
                y: scrollHeight
              }
            }
          }
        }
        this.resizeObserver = new window.ResizeObserver(optimize())
        this.resizeObserver.observe(this.$refs.table.$el.parentElement)
      }
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doResizeDestroy () {
      if (window.ResizeObserver && this.resizeObserver) {
        this.resizeObserver.disconnect()
      }
    },

    /**
     * @description 
     * @param {Array <String>} selectedRowKeys
     * @param {Array <Object>} selectedRows
     * @returns {undefined}
     */
    doTableChange (selectedRowKeys, selectedRows) {
      this.selectedRows = selectedRows
      this.selectedRowKeys = selectedRowKeys
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doTableQuery () {
      this.doDrawerClose()
      this.$refs.table.refresh(true)
    },

    /**
     * @description 
     * @param {Object} options
     * @returns {undefined}
     */
    doPageQuery (options) {
      this.doDrawerClose()
      this.$refs.table.loadData(options)
    },

    /**
     * @description 
     * @param {Object} record
     * @param {String} key
     * @param {Any} value
     * @returns {Promise}
     */
    doActivityChange (record, key, value) {
      // 
      this.$set(record, key, value ? 'Y' : 'N')

      // 
      return this.doTableModify([record]).catch(() => {
        this.$set(record, key, !value ? 'Y' : 'N')
      })
    },

    /**
     * @description 
     * @param {Array} records
     * @returns {Promise}
     */
    doTableModify (records) {
      return roleApi
        .getRoleInfoByUserNo(requestBuilder('update', { userNo: records[0].userNo }))
        .then(res => {
          if (res.code !== '0000') {
            this.$notification.error({
              message: '',
              description: ''
            })
            return Promise.reject(res)
          }

          // 
          Object.assign(records[0], {
            roleId: res.result.map(role => role.roleId)
          })

          // 
          userApi.modifyUserInfo(requestBuilder('update', records[0])).then(res => {
            if (res.code !== '0000') {
              this.$notification.error({
                message: '',
                description: ''
              })
              return Promise.reject(res)
            }
            this.$notification.success({
              message: '',
              description: ''
            })
            this.doTableQuery(true)
          })
        })
    },

    /**
     * @description  - 
     * @returns {undefined}
     */
    doDrawerAdd () {
      this.$refs.userDrawer.doAdd([{}])
    },

    /**
     * @description  - 
     * @param {Object} record
     * @returns {undefined}
     */
    doDrawerEdit (record) {
      this.$refs.userDrawer.doEdit([record])
    },

    /**
     * @description  - 
     * @param {Object} record
     * @returns {undefined}
     */
    doDrawerDel (record) {
      this.$confirm({
        title: '?',
        content: '!',
        okType: 'danger',
        okText: '',
        onOk: () => {
          this.$refs.userDrawer.doDel([record])
        }
      })
    },

    /**
     * @description  - 
     * @returns {undefined}
     */
    doDrawerClose () {
      this.$refs.userDrawer.doClose()
    }
  }
}
</script>

<style lang="less" scoped>
.user-table-container {
  width: 100%;
  height: 100%;
}
</style>
